


.container {
  display: flex;
  flex-direction: column;
  align-items: center;
   background-image: url("bc.png");
   background-color: rgba(58, 138, 212, 0.883);
   width: 100%;
   height: 100%;
  
}

.flecha_pokeball{

  display:flex;
  flex-direction: column;
}


.pokedex {

  font-family: 'DotGothic16', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  background-color: blue;
  border-radius: 5px;
  background: linear-gradient(-45deg, #deadff, #4d3ce7, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 10s ease infinite;
  height:110vh;
}

.flecha{

 height: 70px;
}

.titulo {
  height: 140px;
  padding-top: 30px;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

input {
  margin-top: 20px;
  border: 1px solid white;
  font-family: 'DotGothic16', sans-serif;
  font-size: x-large;
  -webkit-background-clip: text;
  background: linear-gradient(to right, #222222 100%, #280b5744 0%);
  height: 25px !important;
  border-radius: 3px;
  color: white;
  animation-name: input;
animation-duration: 3s;
animation-delay: -1.5s;
width: 300px;
min-width: 10px;
opacity: 1;
}

@keyframes input {
  0% {
   width: 0px;
   opacity: 0;
  }
  20% {
    width: 0px;
    opacity: 0;
   }
  60% {
    width: 10px;
    opacity: 0;
   }
   80% {
    width: 320px;
   }
   100% {
    width: 300px;
   }
}


