@keyframes example {
  0%   {left:10px;}
  25%  { right:10px;width: 10px;height: 10px;}
  50%  {width: 180px;height: 140px;}
  75%  {width: 150px;height: 120px;}
  90% {width: 400px;height: 360px;} 
  100% {width: 380px;height: 340px;} 
  
}

@keyframes texto {
  0%,100% {opacity: 0;}
  
  }



@keyframes foto {
  0%   {height: 20px;}
  25%  { height: 20px;}
  50%  {height: 80px;}
  75%  {height: 130px;}
  100% {height: 120px;} 
}


@keyframes titulo {
